<template>
    <div class="container">
      <ContainerBox colorClass="box-1" :user="user" :points="points" :counter="counter" :lvl="lvl"/>
      <AlbumBox :topUsers="topUsers" :onlineCount="onlineCount" level="lvl"/>
      <listUpper />
      <Buttons :onButtonClick="handleClick"/>
    </div>
  </template>
  
  <script>
import AlbumBox from '@/components/AlbumBox.vue';
import Buttons from '@/components/buttons.vue';
import ContainerBox from '@/components/ContainerBox.vue';
import listUpper from '@/components/listUpper.vue';
import { io } from "socket.io-client";
  export default {
    name: "HomePage",
    components: {
    ContainerBox,
    AlbumBox,
    listUpper,
    Buttons
  },
  data(){
    return{
      telega: window.Telegram.WebApp,
      user:{},
      points:55,
      counter:0,
      lvl:0,
      audio: null, // Объект аудио
      timeoutId: null, // ID таймера
      isAudioPlaying: false, // Флаг для отслеживания состояния звука
      topUsers: [],
      socket: null,
      sounds: Array.from({ length: 72 }, (_, i) => `${i + 1}.mp3`), // Массив с названиями треков
      chosenSounds: [], // Массив для хранения уже выбранных звуков
      onlineCount: 0,
    }
  },
  mounted() {
    this.audio = new Audio(`/sounds/${this.getRandomSound()}`);
    this.audio.loop = true;
    this.socket = io(process.env.VUE_APP_SOCKET_URL);

    // Слушаем обновления топа
    this.socket.on("updateTop", ({ topUsers }) => {
      this.topUsers = topUsers;
    });

    // Отправляем начальные данные
    this.sendData();

    if (window.Telegram.WebApp.initData) {
      console.log(window.Telegram.WebApp);
      this.telega.ready();
      const queryParams = new URLSearchParams(this.telega.initData);
      const userData = {};
      queryParams.forEach((value, key) => {
        userData[key] = value;
      });

      const userName = JSON.parse(userData.user);
      console.log(userName)
      this.user= userName;
    } else {
      this.userId = 'undf';
    }

    this.socket.on("updateOnline", (count) => {
      this.onlineCount = count;
      console.log(`Онлайн: ${count}`);
    });
  },
  methods: {
    getRandomSound() {
      if (this.sounds.length === 0) {
        alert("Все звуки использованы!");
        return null;
      }

      const randomIndex = Math.floor(Math.random() * this.sounds.length);
      const chosenSound = this.sounds[randomIndex];

      // Удаляем выбранный звук из массива
      this.sounds.splice(randomIndex, 1);

      // Сохраняем выбранный звук (если нужно)
      this.chosenSounds.push(chosenSound);

      return chosenSound;
    },
    sendData() {
      this.socket.emit("click", {
        userId: this.user.id,
        clicks: this.points,
        photo: this.user.photo_url,
        name: this.user.first_name
      });
    },
    handleClick() {
      this.counter++;
      this.points = this.lvl * 100 + this.counter;
      this.sendData();

      // Повышение уровня при каждом 100-м нажатии
      if (Math.floor(this.counter / 100) > 0) {
        this.lvlUp();
      }

      // Проигрываем звук при клике
      if (!this.isAudioPlaying) {
        this.audio.play();
        this.isAudioPlaying = true;
      }

      // Сбрасываем таймер для остановки звука
      clearTimeout(this.timeoutId);
      this.timeoutId = setTimeout(() => {
        this.stopAudio();
      }, 300);
    },
    stopAudio() {
      this.audio.pause();
      this.audio.currentTime = 0;
      this.isAudioPlaying = false;
      this.counter = 0;
    },
    lvlUp() {
      this.lvl++;
      this.counter = 0;
      this.stopAudio();

      // Меняем звук на новый при повышении уровня
      this.audio = new Audio(`/sounds/${this.getRandomSound()}`);
      this.audio.loop = true;
    },
  },
};
</script>
  

<style>
/* Глобальные стили для цветов */
.box-1 { background-color: #212529; }

.container{
    margin-top: 10%;
}
