<template>
    <div class="knob-container" @click="onClick">
      <svg class="knob-wrapper" viewBox="0 0 100 100">
        <!-- Фон круга -->
        <circle class="knob-bg" cx="50" cy="50" r="45"></circle>
        <!-- Активная часть -->
        <circle
          class="knob-circle"
          :style="circleStyle"
          cx="50"
          cy="50"
          r="45"
        ></circle>
      </svg>
      <div class="knob-text">{{progress}}</div>
    </div>
  </template>
  
  <script>
  export default {
    name: "KnobIndicator",
    props: {
      progress: {
        type: Number,
        required: true,
        default: 0,
      },
      maxProgress: {
        type: Number,
        required: true,
        default: 100,
      },
      color: {
        type: String,
        default: "orange",
      },
      onClickHandler: {
        type: Function,
        default: null, // Позволяет передать внешний обработчик кликов
      },
    },
    computed: {
      percentage() {
        return Math.min(this.progress / this.maxProgress, 1) * 100;
      },
      circleStyle() {
        const perimeter = 2 * Math.PI * 45; // Периметр круга (2πr)
        const offset = perimeter - (this.percentage / 100) * perimeter;
        return {
          strokeDasharray: `${perimeter}`,
          strokeDashoffset: offset,
          stroke: this.color,
        };
      },
    },
    methods: {
      onClick() {
        if (this.onClickHandler) {
          this.onClickHandler();
        }
      },
    },
  };
  </script>
  
  <style scoped>
  .knob-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 30px;
    height: 30px;
    cursor: pointer;
  }
  
  .knob-wrapper {
    width: 100%;
    height: 100%;
  }
  
  .knob-bg {
    stroke: #e0e0e0;
    stroke-width: 12;
    fill: none;
  }
  
  .knob-circle {
    stroke-width: 12;
    fill: none;
    stroke-linecap: round;
    transform: rotate(-90deg);
    transform-origin: center;
    transition: stroke-dashoffset 0.3s ease;
  }
  
  .knob-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 8px;
    font-weight: bold;
    color: #ffffff;
  }
  </style>